<template>
  <div class="shopManageCom">
    <!-- 顶部搜索框 -->
    <div class="top">
      <div class="search">
        店铺名称：
        <el-input v-model="keywords"></el-input>
        <el-button type="primary" size="small" @click="clickSearch">搜索</el-button>
      </div>
    </div>
    <!-- 新增店铺 -->
    <div class="addShop">
      <el-button type="primary" size="medium" @click="clickAddShop">新增店铺</el-button>
    </div>

    <!-- 店铺列表 -->
    <div class="bottom">
      <div class="right">
        <!-- 分页 -->
        <div class="fr total">共{{ total }}笔</div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[10, 100, 200]"
          :page-size="10"
          layout="sizes, prev, pager, next"
          :total="total"
          class="fr"
        ></el-pagination>
      </div>
      <el-table :data="shopList" style="width: 100%" header-align="center">
        <el-table-column label="编号" prop="id" min-width="50" align="center"></el-table-column>
        <el-table-column label="店铺名称" prop="store_name" min-width="150" align="center"></el-table-column>
        <el-table-column label="卖家编号" prop="merchant_number" min-width="100" align="center"></el-table-column>
        <el-table-column label="授权国家" prop="language" min-width="100" align="center"></el-table-column>
        <el-table-column label="数据语言" prop="language_name" min-width="100" align="center"></el-table-column>
        <!-- <el-table-column label="关联账号" prop="store_name" min-width="100" align='center'></el-table-column> -->
        <!-- <el-table-column label="自动同步订单" prop="store_name" min-width="100" align='center'></el-table-column> -->
        <!-- <el-table-column label="授权时间" prop="create_at" min-width="100" align='center'></el-table-column> -->
        <el-table-column label="状态" prop="status" min-width="50" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status == 1">已授权</span>
            <span v-else>待授权</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" min-width="100" align="center">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editShop(scope.row)">编辑</el-button>
            <el-button
              type="info"
              size="mini"
              @click="forbiddenShop(scope.row)"
              v-if="scope.row.status == 1"
            >禁用</el-button>
            <el-button
              type="warning"
              size="mini"
              @click="forbiddenShop(scope.row)"
              v-if="scope.row.status == 2"
            >启用</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 新增店铺对话框 -->
    <el-dialog
      title="新增店铺"
      :visible.sync="addShopDialogVisible"
      width="40%"
      class="insertImgDialog"
      @close="addShopClose"
    >
      <el-form
        class="addShopForm"
        :model="addShopForm"
        :rules="addShopFormRules"
        ref="addShopFormRef"
      >
        <el-form-item label="店铺名称：" prop="store_name">
          <el-input placeholder="店铺名称最多不超过30个字符" v-model="addShopForm.store_name"></el-input>
        </el-form-item>
        <el-form-item label="数据语言：" prop="language_name">
          <el-radio-group v-model="addShopForm.language_name">
            <el-radio label="中文"></el-radio>
            <el-radio label="英文"></el-radio>
            <el-radio label="繁体"></el-radio>
            <el-radio label="日文"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="开户站：" prop="language_id">
          <!-- <el-input placeholder="请选择" v-model="addShopForm.language_id"></el-input> -->
          <el-select v-model="addShopForm.language_id" placeholder="请选择">
            <el-option
              :label="item.language_name"
              :value="item.id"
              v-for="(item,index) in OpeningStationList"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="卖家编号：" prop="merchant_number">
          <el-input placeholder="Merchant ID" v-model="addShopForm.merchant_number"></el-input>
        </el-form-item>
        <el-form-item class="instruction">
          <span slot="label">说明！</span>
          <p>1、自定义一个店铺名称，方便您以后进行多店铺管理！</p>
          <p>2、店铺的定制产品将按所选语言生成名称和标签。</p>
          <p>3、开户站、卖家编号成功提交后，不允许修改，该数据仅限用于平台反跟卖保护支持，如需调整，请联系客服。</p>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addShopDialogVisible = false" size="small">取 消</el-button>
        <el-button
          type="primary"
          @click="confirmAddShop()"
          size="small"
          style="margin-right: 20px;"
        >保 存</el-button>
      </span>
    </el-dialog>
    <!-- 编辑店铺对话框 -->
    <el-dialog
      title="编辑店铺"
      :visible.sync="editShopDialogVisible"
      width="40%"
      class="insertImgDialog"
      @close="editShopClose"
    >
      <el-form
        class="addShopForm"
        :model="editShopForm"
        :rules="addShopFormRules"
        ref="editShopFormRef"
      >
        <el-form-item label="店铺名称：" prop="store_name">
          <el-input placeholder="店铺名称最多不超过30个字符" v-model="editShopForm.store_name"></el-input>
        </el-form-item>
        <el-form-item label="数据语言：" prop="language_name">
          <el-radio-group v-model="editShopForm.language_name">
            <el-radio label="中文"></el-radio>
            <el-radio label="英文"></el-radio>
            <el-radio label="繁体"></el-radio>
            <el-radio label="日文"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="开户站：" prop="language_id">
          <!-- <el-input placeholder="请选择" v-model="addShopForm.language_id"></el-input> -->
          <el-select v-model="editLanguage" placeholder="请选择" disabled>
            <el-option
              :label="item.language_name"
              :value="item.id"
              v-for="(item,index) in OpeningStationList"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="卖家编号：" prop="merchant_number">
          <el-input placeholder="Merchant ID" v-model="editShopForm.merchant_number" disabled></el-input>
        </el-form-item>
        <el-form-item class="instruction">
          <span slot="label">说明！</span>
          <p>1、自定义一个店铺名称，方便您以后进行多店铺管理！</p>
          <p>2、店铺的定制产品将按所选语言生成名称和标签。</p>
          <p>3、开户站、卖家编号成功提交后，不允许修改，该数据仅限用于平台反跟卖保护支持，如需调整，请联系客服。</p>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editShopDialogVisible = false" size="small">取 消</el-button>
        <el-button
          type="primary"
          @click="confirmEditShop()"
          size="small"
          style="margin-right: 20px;"
        >保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      addShopDialogVisible: false, //新增店铺对话框显示隐藏
      editShopDialogVisible: false, //编辑店铺对话框显示隐藏
      editShopForm: {
        id: '',
        store_name: '', //店铺名称
        language_name: '英文',
        language_id: '',
        merchant_number: '',
        status: ''
      },
      addShopForm: {
        store_name: '', //店铺名称
        language_name: '英文',
        language_id: '',
        merchant_number: ''
      },
      addShopFormRules: {
        store_name: [
          { required: true, message: '请输入店铺名称', trigger: 'blur' },
          { min: 1, max: 30, message: '长度在 0 到 30 个字符', trigger: 'blur' }
        ],
        language_name: [
          { required: true, message: '请选择数据语言', trigger: 'blur' }
        ],
        language_id: [
          { required: true, message: '请选择开户站', trigger: 'blur' }
        ],
        merchant_number: [
          { required: true, message: '请输入正确的卖家编号', trigger: 'blur' }
        ]
      },
      OpeningStationList: [], //开户站列表
      shopList: [], //店铺列表
      editLanguage: '', //编辑时归家
      currentPage: 1,
      total: 0,
      keywords:'',//搜索关键字
    }
  },
  methods: {
    handleSizeChange(val) {
      this.getShopList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getShopList()
    },
    // 编辑店铺
    editShop(data) {
      this.editShopDialogVisible = true
      this.editShopForm.id = data.id
      this.editShopForm.store_name = data.store_name
      this.editShopForm.language_name = data.language_name
      this.editShopForm.language_id = data.language_id
      this.editShopForm.merchant_number = data.merchant_number
      this.editLanguage = data.language
      this.editShopForm.status = data.status
    },
    // 监听编辑对话框关闭
    editShopClose() {
      this.$refs.editShopFormRef.resetFields()
    },
    // 监听对话框关闭
    addShopClose() {
      this.$refs.addShopFormRef.resetFields()
    },
    // 禁用启用店铺确定
    async forbiddenShop(data) {
      if (data.status == '1') {
        this.$confirm('此操作将禁用该店铺, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            const res = await this.api.storeEdit({
              id: data.id,
              store_name: data.store_name,
              language_name: data.language_name,
              status: 2
            })
            if (res.data.code == 200) {
              this.$message({
                type: 'success',
                message: '禁用成功'
              })
              this.getShopList()
            }
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消禁用'
            })
          })
      } else {
        this.$confirm('此操作将启用该店铺, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            const res = await this.api.storeEdit({
              id: data.id,
              store_name: data.store_name,
              language_name: data.language_name,
              status: 1
            })
            if (res.data.code == 200) {
              this.$message({
                type: 'success',
                message: '启用成功'
              })
              this.getShopList()
            }
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消启用'
            })
          })
      }
    },
    // 新增店铺确定
    async confirmAddShop() {
      const res = await this.api.storeAdd(this.addShopForm)
      if (res.data.code == 200) {
        this.$message({
          message: '添加成功',
          type: 'success'
        })
        this.addShopDialogVisible = false
        this.getShopList()
      }
    },
    // 编辑店铺确定
    async confirmEditShop() {
      const res = await this.api.storeEdit(this.editShopForm)
      if (res.data.code == 200) {
        this.$message({
          message: '修改成功',
          type: 'success'
        })
        this.editShopDialogVisible = false
        this.getShopList()
      }
    },
    // 点击新增店铺，显示对话框
    clickAddShop() {
      this.addShopDialogVisible = true
    },
    clickSearch() {
      this.currentPage = 1
      this.getShopList()

    },
    // 获取开户站列表
    async getOpeningStationList() {
      const res = await this.api.storeCountry()
      if (res.data.code == 200) {
        this.OpeningStationList = res.data.data
      }
    },
    // 获取店铺列表
    async getShopList() {
      const res = await this.api.storeList({ page: this.currentPage,store_name: this.keywords })
      if (res.data.code == 200) {
        this.shopList = res.data.data.data
        this.total = res.data.data.total
      }
    }
  },
  created() {
    this.getShopList()
    this.getOpeningStationList()
  }
}
</script>
<style lang="scss" scoped>
.shopManageCom {
  text-align: left;
  background-color: #fff;
  .top {
    padding: 20px;
    border-bottom: 1px solid #f0f0f0;
    .search {
      .el-input {
        width: 30%;
      }
      .el-button {
        margin-left: 10px;
      }
    }
  }
  .addShop {
    padding: 15px 20px;
    border-bottom: 1px solid #f0f0f0;
  }
  .addShopForm {
    .el-form-item {
      margin-bottom: 0 !important;
      .el-input {
        width: 60%;
      }
      .el-select {
        width: 60% !important;
      }
    }
  }
  .bottom {
    padding: 0 20px;
    .right {
      padding: 10px;
      padding-bottom: 40px;
    }
    .total {
      margin-right: 50px;
      margin-left: 30px;
      line-height: 30px;
    }
  }
}
</style>


